.top-message {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  z-index: 100;
  background-color: $accent;
  &__container {
    height: 28px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__text {
    font-weight: 300;
    font-size: 13px;
    color: #fff;
  }
}
